/* .register */
.register {
    width: 100%;
    height: max-content;
    background-image: url("/public/img/background02.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-size: 100% 100%;
    position: relative;
}

.register::after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    background: linear-gradient(to right, transparent, rgba(0, 0, 0, 0.8)) 80%;
}

.register .title {
    font-size: 2rem;
    text-align: center;
    font-weight: 500;
    color: #fff;
    text-transform: uppercase;
    letter-spacing: 0.2rem;
    line-height: 3rem;
}

.register-content {
    max-width: 100%;
    min-width: 300px;
    border-radius: 10px;
    box-shadow: 0 0 5px 0 rgb(255, 255, 255, 0.2);
    background-color: rgb(0, 0, 0, 0.5);
    position: relative;
    z-index: 9;
}

.frmRegister {
    max-width: max-content;
    min-width: 250px;
    width: 100%;
    border-radius: 7px;
}

